<script>
import BottomNavigationMenu from "@/components/BottomNavigationMenu";
import HeaderUserBar from "@/components/HeaderUserBar";
import axios from "axios";
import prize_ru from '../files/prizes-ru.svg'
import house_ru from '../files/house-ru.svg'
import prize_uz from '../files/prizes-uz.svg'
import house_uz from '../files/house-uz.svg'

export default {
  name: "Home",
  components: {HeaderUserBar, BottomNavigationMenu},
  data: () => ({
    prize_ru: prize_ru,
    house_ru: house_ru,
    prize_uz: prize_uz,
    house_uz: house_uz
  }),
  async mounted() {

    new Swiper(".banner-collage", {
      effect: "cards",
      grabCursor: true,
      autoplay: {
        delay: 2500,
      },
      slidesPerView: 'auto',
      // loop: true,
    });

    new Swiper(".service-cards", {
      watchSlidesProgress: true,
      slidesPerView: 2.9,
    })


    Telegram.WebApp.MainButton.hideProgress();
    Telegram.WebApp.MainButton.hide();
    Telegram.WebApp.BackButton.hide();
  },
  methods: {
    async subscripInvoice()  {
      await axios.post(`https://server.gold-ticket.uz/api/invoice/${this.$store.state.user_id}`, {'lang': this.$store.state.lang})
          .then(responce => {
            if (responce.data.ok) {
              if (this.$store.state.lang === 'ru') {
                Telegram.WebApp.showPopup({
                  'title': '✅ Успешно',
                  'message': 'Бот вам отпрвил счёт для оплаты',
                  'buttons': [
                    {
                      'id': 'success',
                      'type': 'ok',
                      'text': "Продолжить"
                    }
                  ]
                })
              } else {
                Telegram.WebApp.showPopup({
                  'title': '✅ Muvofaqiyatli',
                  'message': 'Bot sizga tolov uchun hisob yubordi',
                  'buttons': [
                    {
                      'id': 'success',
                      'type': 'ok',
                      'text': "Davom etish"
                    }
                  ]
                })
              }
              Telegram.WebApp.onEvent('success', Telegram.WebApp.close())
            } else {
              if (this.$store.state.lang === 'ru') {
                Telegram.WebApp.showPopup({
                  'title': '⚠️Ошибка!',
                  'message': 'Произошло ошибка притязания счета оплаты\n\nПовторите попытку больше ',
                  'buttons': [
                    {
                      'id': 'success',
                      'type': 'ok',
                      'text': "Повторить"
                    }
                  ]
                })
              } else {
                Telegram.WebApp.showPopup({
                  'title': '⚠️Xato!',
                  'message': 'Tolov hisibi yaratishda hatolik yuz berdi!\n\nKeyinroq qayta urunib koring',
                  'buttons': [
                    {
                      'id': 'success',
                      'type': 'ok',
                      'text': "Davom etish"
                    }
                  ]
                })
              }
            }
          })
    },
  },
}
</script>
